







































@import "@design";

.app-container {
  display: flex;
  flex-direction: row;
}
.sidebar-container {
  height: 100vh;
  min-width: 250px;
  max-width: 250px;
  background-color: $color-primary;
  color: white;
  padding: 30px;
  box-sizing: border-box;

  a {
    color: white;
    text-decoration: none;
  }
}
.sidebar-bottom {
  position: absolute;
  bottom: 30px;
}
.navbar {
  margin-top: 15px;
  li {
    padding: 10px 0;
  }
}
