

















































































































@import "@design";

.main-inner {
  display: flex;
}

.note {
  &-container {
    background: #f4f6f7;
    height: 100vh;
    flex: 0 0 350px;
  }
  &-editor {
    flex-grow: 1;
    padding: 30px;
  }
  &-toolbar {
    border-bottom: 1px solid $color-border;
    padding: 30px;
  }

  &-list {
    padding: 30px;
    overflow-y: scroll;
    box-sizing: border-box;
    height: calc(100% - 100px);
  }
  &__item {
    background: white;
    padding: 30px 20px;
    clip-path: polygon(80% 0, 100% 20%, 100% 100%, 0 100%, 0 0);
    margin-bottom: 15px;
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0px 0px 6px #a3a3a31a;
  }
  &__title {
    padding-bottom: 15px;
    border-bottom: 1px solid $color-border;
  }
}
.toolbar__input {
  padding: 10px;
  margin: 0;
  width: 100%;
  border: none;
  border-radius: 4px;
  background: #e6e6e6;
  &:focus {
    outline: none;
  }
}
